import { ElButton, ElMessage, ElInput } from "element-plus";
export default {
  components: {
    ElInput
  },
  data() {
    return {
      // 信息
    };
  },
  methods: {
    // 日期处理方法
    async generateAnalysis() {
      this.loading = true;
      try {
        // 信息汇总
        let prompt = "我需要你使用中医外科的知识对我的症状进行分析，这是信息：";
        let msg = prompt;
        const encodedDescription = encodeURIComponent(msg);
        try {
          const newWindow = window.open(`/#/chat?user_input=${encodedDescription}`);
          if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
            window.location.href = `/#/chat?user_input=${encodedDescription}`;
          }
        } catch (error) {
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        }
      } catch (error) {
        ElMessage.error('生成分析信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    }
  }
};